button {
	cursor: pointer;
	background-color: var(--color-main-y);
	font-family: Karla;
	border: none;
	transition: box-shadow 150ms;
}

button:hover {
	box-shadow: 0 calc(var(--spacing-small) / 2) var(--spacing-small)
		rgba(70, 70, 70, 0.3);
}

button:active {
	background-color: rgb(192, 162, 14);
}
